import React from 'react'
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import TimelineOppositeContent, {
  timelineOppositeContentClasses,
} from '@mui/lab/TimelineOppositeContent';

import { ReactComponent as Tick } from '../../../assets/images/Tick.svg';
import { ReactComponent as Ellipse } from '../../../assets/images/Ellipse.svg';
import { ReactComponent as Horizontal } from '../../../assets/images/Horizontal.svg';
import Vertical from '../../../assets/images/Vertical.png';

export default function RoadMap() {
  return (
    <div className='roadmap'>
        <div className='roadmap_main'>
            <div className='roadmap_title'>
                RoadMap
            </div>
            <div className='bridge'>
                <div className='vertical_1'><img  src={Vertical}/></div>
                <div className='horizontal_css_1'></div>
                <div className='vertical_2'><img  src={Vertical}/></div>
                <div className='horizontal_css_2'></div>
            </div> 
            <div className='roadmap_year'>
                <div className='year'>2022</div>
            </div>
            <div className='roadmap_comp1'>
                <div className='month'>OCT</div>
                <div className='timeline_div'>
                    <Timeline
                        sx={{
                        [`& .${timelineOppositeContentClasses.root}`]: {
                            flex: 0,
                        },
                        }}
                    >
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Tick style={{width:'25px'}}/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Private Seed Protal</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Private Seed Round 1</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                            </TimelineSeparator>
                            <TimelineContent>Platform Beta Design</TimelineContent>
                        </TimelineItem>
                    </Timeline>
                </div>
            </div>
            
            <div className='roadmap_comp2'>
                <div className='month'>NOV</div>
                <div className='timeline_div'>
                    <Timeline
                        sx={{
                        [`& .${timelineOppositeContentClasses.root}`]: {
                            flex: 0,
                        },
                        }}
                    >
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Platform Beta Testing</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Platform Beta Launch</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Seed Round 2 Sale</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                            </TimelineSeparator>
                            <TimelineContent>Token Launch</TimelineContent>
                        </TimelineItem>
                    </Timeline>
                </div>
            </div>
            <div className='roadmap_comp3'>
                <div className='month'>DEC</div>
                <div className='timeline_div'>
                    <Timeline
                        sx={{
                        [`& .${timelineOppositeContentClasses.root}`]: {
                            flex: 0,
                        },
                        }}
                    >
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Platform Update</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                                <TimelineConnector sx={{
                                    backgroundColor: "#0288d1",
                                }}/>
                            </TimelineSeparator>
                            <TimelineContent>Increase Token Usecase</TimelineContent>
                        </TimelineItem>
                        <TimelineItem>
                            <TimelineOppositeContent color="text.secondary">
                                <Ellipse/>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="info"/>
                            </TimelineSeparator>
                            <TimelineContent>Listings</TimelineContent>
                        </TimelineItem>
                    </Timeline>
                </div>
            </div>
            
        </div>
    </div>
  )
}
